*, :before, :after {
  box-sizing: border-box;
  padding: 0;
}

html {
  font-family: Roboto, sans-serif;
}

body, p, h1 {
  margin: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.header__logo {
  display: block;
}

.header__nav {
  padding: 10px 0;
}

.header__nav-items {
  text-transform: uppercase;
  align-items: center;
  padding: 0;
  font-size: 12px;
  font-weight: 500;
  display: flex;
}

.header__nav-item {
  text-align: center;
  margin: 0 10px;
  list-style: none;
}

.header__nav-item:first-child {
  margin-left: 0;
}

.header__nav-item:last-child {
  margin-right: 0;
}

.header__nav-link {
  color: #060b35;
  padding: 22.5px 0;
  text-decoration: none;
  transition: color .5s;
}

.header__nav-link:hover, [data-qa="nav-hover"]:hover {
  color: #00acdc;
}

a.is-active {
  color: #00acdc;
  position: relative;
}

a.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: -1px;
}

.main {
  --column-count: 4;
  grid-template-columns: repeat(var(--column-count), 1fr);
  justify-content: center;
  gap: 46px 48px;
  max-width: fit-content;
  margin: 0 auto;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 300px) {
  .main {
    --column-count: 1;
  }
}

@media (width >= 488px) {
  .main {
    --column-count: 2;
  }
}

@media (width >= 768px) {
  .main {
    --column-count: 3;
  }
}

@media (width >= 1024px) {
  .main {
    --column-count: 4;
  }
}

.card {
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  align-items: center;
  width: 200px;
  padding: 32px 16px 16px;
  transition: transform .3s;
  display: flex;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover__details-name {
  color: #34568b;
}

.card__product {
  display: flex;
}

.card__product-image {
  width: 160px;
  height: 134px;
}

.card__details {
  margin: 40px 0 0;
}

.card__details-name {
  color: #060b35;
  width: 166px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__details-code {
  color: #616070;
  margin: 4px 0 0;
  font-size: 10px;
  font-weight: 400;
}

.card__details-price {
  color: #616070;
  justify-content: space-between;
  align-items: center;
  margin: 24px 0 0;
  font-size: 12px;
  font-weight: 400;
  display: flex;
}

.card__details-price span {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
}

.card__reviews {
  justify-content: space-between;
  align-items: center;
  margin: 16px 0 0;
  display: flex;
}

.card__reviews-stars {
  margin: 0;
  display: flex;
}

.card__reviews-count {
  color: #060b35;
  font-size: 10px;
  font-weight: 400;
}

.card__button-buy {
  color: #fff;
  text-transform: uppercase;
  background-color: #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  margin: 16px 0 0;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: flex;
}

.card__button-buy[data-qa="card-hover"] {
  transition: color .3s;
}

.card__button-buy[data-qa="card-hover"]:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.card__reviews-stars--4 .star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}
